import React, { Component } from 'react';
import { toast } from "react-toastify";
import { Modal } from 'react-bootstrap';
import Select from 'react-select';
import { createCollection } from '../../../../Services/Actions/homeActions'
class CreateCollectionModal extends Component {
    constructor(props) {
        super(props);

    }
    state = {
        addRule: false,
        collectioName: null
    };

    onClickCreate = event => {
        var self = this
        const { collectioName } = this.state;
        if (collectioName && String(collectioName).length > 0) {
            var toastID = toast.loading("Loading...", { autoClose: 3000 })
            createCollection({ name: collectioName }).then((response) => {
                toast.dismiss(toastID)
                toast.success("Collection has been successfully created.")
                self.props.addCollectionCallbackWithAPI(false)
            })
        }
    }
    onChangeCollectionName = event => {
        this.setState({ collectioName: event.target.value })
    }

    handleClose = event => this.props.addCollectionCallback(false)

    addRuleByFun = (e) => {
        // console.log('e.currentTarget.value', e.currentTarget.value)
        // this.setState({
        //     addRule:e.currentTarget.value
        // })
        switch (e.currentTarget.value) {
            case 'byHandle':
                return 'Handle'
            case 'byHashtag':
                return 'Hashtag'
            case 'byTags':
                return 'Tags'
            default:
                break;
        }
    }


    render() {
        const { addCollectionModal } = this.props
        const { addRule, collectioName } = this.state

        return <Modal show={addCollectionModal} onHide={this.handleClose}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Create New Collection</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="mx-xl-4 px-2 px-lg-4 mx-0 pt-4">
                    <div className="row mb-4 mb-xl-5">
                        <label className="col-sm-3 text-muted mb-2">Collection Name</label>
                        <div className="col-sm-9">
                            <input type="text" name="company" className="form-control" placeholder="Enter collection name..." onChange={this.onChangeCollectionName} value={collectioName} />
                        </div>
                    </div>
                    {addRule ?
                        <div>
                            <div className="row mb-4 mb-xl-5">
                                <div className="offset-sm-3 col-sm-9">
                                    <div className="d-flex align-items-center pt-1">
                                        <div className="form-check me-3 me-xl-5">
                                            <input className="form-check-input" type="radio" name="addRuleOn" value="default" id="default" onChange={this.addRuleFun} defaultChecked />
                                            <label className="form-check-label text-muted" htmlFor="default">Default</label>
                                        </div>
                                        <div className="form-check">
                                            <input className="form-check-input" type="radio" name="addRuleOn" value="addRule" id="addRule" onChange={this.addRuleFun} />
                                            <label className="form-check-label text-muted" htmlFor="addRule">Add Rule</label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row mb-4 mb-xl-5">
                                <label className="col-sm-3 text-muted mb-2">Rule Name</label>
                                <div className="col-sm-9">
                                    <input type="text" name="ruleName" className="form-control" placeholder="Enter rule name..." defaultValue="" />
                                </div>
                            </div>
                            <div className="row mb-4 mb-xl-5">
                                <div className="offset-sm-3 col-sm-9">
                                    <div className="d-flex align-items-center pt-1">
                                        <div className="form-check me-3 me-xl-5">
                                            <input className="form-check-input" type="radio" name="feedType" value="byHandle" id="byHandle" onChange={this.addRuleByFun} defaultChecked />
                                            <label className="form-check-label text-muted" htmlFor="byHandle">By Handle</label>
                                        </div>
                                        <div className="form-check me-3 me-xl-5">
                                            <input className="form-check-input" type="radio" name="feedType" value="byHashtag" id="byHashtag" onChange={this.addRuleByFun} />
                                            <label className="form-check-label text-muted" htmlFor="byHashtag">By Hashtag</label>
                                        </div>
                                        <div className="form-check">
                                            <input className="form-check-input" type="radio" name="feedType" value="byTags" id="byTags" onChange={this.addRuleByFun} />
                                            <label className="form-check-label text-muted" htmlFor="byTags">By Tags</label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row mb-4 mb-xl-5">
                                <div className="offset-sm-3 col-sm-9">
                                    <input type="text" name="handleName" className="form-control" placeholder="Enter handle name..." defaultValue="" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-sm-6">

                                </div>
                                <div className="col-sm-6">

                                </div>
                            </div>
                        </div>
                        : null
                    }
                </div>
            </Modal.Body>
            <Modal.Footer className="p-3">
                <div className="mx-xl-4 px-2 px-lg-4 mx-0">
                    <button type="button" className="btn btn-secondary btn-sm btn-min-w me-3" onClick={this.handleClose}>Cancel</button>
                    <button type="button" className="btn btn-primary btn-sm btn-min-w" onClick={this.onClickCreate}>Create</button>
                </div>
            </Modal.Footer>
        </Modal>
    }
}

export default CreateCollectionModal;
